<template>
  <div class="user">
    <van-nav-bar title="我的收藏" left-text="返回" left-arrow fixed @click-left="$router.go(-1)" />
    <div class="user-info" v-if="user">
      <div class="haead-img-bg">
        <img class="img" :src="user.avatar" lazy-load="false" />
      </div>
      <div class="info">
        <div class="img-name">
          <div class="user-img-container">
            <img class="user-img" :src="user.avatar" lazy-load="false" />
          </div>
          <div class="user-name">
            <span class="name">{{user.name}}</span>
            <div class="info-item id">
              <span class="title">id:</span>
              <span>{{user._id}}</span>
            </div>
          </div>
          <div class="like">
            <van-icon name="good-job" color="#ffffff" size="50rpx" />
            <span class="like-num">17572</span>
          </div>
        </div>
        <div class="info-form">
          <div class="info-form-item" v-for="(value,key) in user" :key="key">
            <van-icon name="photograph" />
            <span class="info-form-item-title">{{key}}:</span>
            <span class="info-form-item-context">{{value}}</span>
          </div>
        </div>
      </div>
      <!-- <div class="handle-btn" wx:if="{{currentUser._id === user._id}}">
        <van-button plain type="warning" bind:click="cleanToken">清除token</van-button>
        <van-button type="danger" bind:click="handleLogout">退出登录</van-button>
        <van-button type="primary" bind:click="changeUser">切换用户</van-button>
      </div>-->
    </div>
    <div class="error" v-else>
      <no-data>数据错误,请返回!</no-data>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: null
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    async getData() {
      let { id } = this.$route.params;
      let data = await this.$store.dispatch("getUser", id);
      this.user = data;
    }
  }
};
</script>

<style lang="scss" scoped>
@function rpxrem($arg) {
  @return ((($arg / ($arg * 0 +1))+0rem) / 75);
}
view {
  display: block;
}

.user {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-size: 16px;
}
.user-info {
  width: 100%;
  padding-bottom: 50px;

  .haead-img-bg {
    width: 100%;
    height: rpxrem(400rpx);
    overflow: hidden;
    z-index: -1;
    position: relative;
    filter: blur(5px) brightness(80%);

    .img {
      width: 100%;
    }
  }

  .info {
    width: 100%;
    margin-top: rpxrem(-140rpx);
    z-index: 5;

    .img-name {
      display: flex;
      flex-direction: row;
      width: 100%;
      height: rpxrem(250rpx);
      box-sizing: border-box;
      padding-top: rpxrem(45rpx);

      .user-img-container {
        width: rpxrem(224rpx);
        padding: rpxrem(20rpx);
        height: rpxrem(200rpx);
        box-sizing: border-box;
        padding-top: 0;

        .user-img {
          width: rpxrem(180rpx);
          height: rpxrem(180rpx);
        }
      }

      .user-name {
        display: flex;
        flex-direction: column;
        padding: rpxrem(20rpx) 0;
        box-sizing: border-box;
        width: 100%;
        max-width: rpxrem(370rpx);

        .name {
          height: rpxrem(80rpx);
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          width: 100%;
          font-size: rpxrem(45rpx);
          color: #fff;
        }

        .id {
          font-style: rpxrem(32rpx);
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }

        .info-item {
          .title {
            margin-right: rpxrem(10rpx);
          }
        }
      }

      .like {
        text-align: center;
        width: rpxrem(100rpx);
        height: rpxrem(100rpx);
        line-height: rpxrem(24rpx);

        .like-num {
          color: #fff;
          font-size: rpxrem(24rpx);
        }
      }
    }

    .info-form {
      width: 100%;
      box-sizing: border-box;
      padding: rpxrem(20rpx);

      .info-form-item {
        display: flex;
        flex-direction: row;
        align-items: center;
        border-top: 1px solid $uni-bg-color-grey;
        padding: 3px 0;

        &:first-of-type {
          border-top: 0;
        }

        .info-form-item-title {
          font-weight: bold;
          padding: rpxrem(20rpx);
        }

        .info-form-item-context {
          word-break: break-all;
          word-wrap: break-word;
        }
      }
    }
  }

  .handle-btn {
    position: fixed;
    padding: rpxrem(20rpx) 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    box-sizing: border-box;
    text-align: center;
    background-color: #fff;

    van-button {
      width: 30%;
      height: 100%;

      button {
        height: 100%;
        white-space: nowrap;
      }
    }
  }
}
</style>